import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import { combineReducers, createStore } from 'redux';
import { Provider } from 'react-redux';
import todosReducer from './reducers/todos.js';

console.log('INFO', process.env.REACT_APP_INFO);

let store = createStore( combineReducers({
  todos: todosReducer
}), window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__() );

ReactDOM.render(
  <Provider store={ store }>
    <App />
  </Provider>, document.getElementById('root')
);

let defaultData = [
  { id: 1, val: "React" },   { id: 2, val: "Angular" }, { id: 3, val: "Rails" },
  { id: 4, val: "Node.js" }, { id: 5, val: "jQuery" } ];

store.dispatch({type:'LOAD_DEFAULT', data:defaultData});

registerServiceWorker();



